'use client';

import { useState, Suspense } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { clientResolveTenant } from "@/services/apiService";
import { Capacitor } from "@capacitor/core";
import { checkAndRequestLocationPermissions, getConnectedWifiInfo } from "@/lib/wifi";
import { API_BASE_URL } from "@/config";
import LandingPage from "@/components/LandingPage";

type InitState = 'loading' | 'permission_denied' | 'wifi_disconnected' | 'tenant_not_found' | 'error';

function HomeContent() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const [state, setState] = useState<InitState | 'idle'>('idle');
    const [errorMessage, setErrorMessage] = useState<string | null>(null);

    const initConnect = async () => {
        setState('loading');
        try {
            const isNative = Capacitor.isNativePlatform();

            const nasId = searchParams.get('nas_id') || undefined;
            let bssid: string | undefined = undefined;

            // Native app: attempt to get BSSID
            if (isNative) {
                try {
                    const wifiCheckPromise = async () => {
                        const hasPermission = await checkAndRequestLocationPermissions();
                        if (hasPermission) {
                            const wifiInfo = await getConnectedWifiInfo();
                            if (wifiInfo.isConnected && wifiInfo.bssid) {
                                bssid = wifiInfo.bssid;
                            }
                        }
                    };

                    const timeoutPromise = new Promise((_, reject) =>
                        setTimeout(() => reject(new Error("Timeout")), 1500)
                    );

                    await Promise.race([wifiCheckPromise(), timeoutPromise]);
                } catch (e) {
                    console.warn("WiFi detection skipped or failed:", e);
                }
            }

            // Resolve tenant
            const tenantSlug = await clientResolveTenant(nasId, bssid);

            if (tenantSlug) {
                router.replace(`/${tenantSlug}`);
            } else {
                setState('tenant_not_found');
            }
        } catch (err: any) {
            console.error("Failed to resolve tenant", err);
            setErrorMessage("Errore durante l'identificazione dello spazio chat.");
            setState('error');
        }
    };

    // If idle, show Landing Page
    if (state === 'idle') {
        return <LandingPage onConnect={initConnect} />;
    }

    // Loading state
    if (state === 'loading') {
        return (
            <div className="h-screen w-full flex flex-col items-center justify-center bg-white p-4">
                <div className="w-24 h-24 mb-6 flex items-center justify-center">
                    <img src="/local_logo.svg" alt="Local Logo" className="w-full h-full object-contain animate-pulse" />
                </div>
                <div className="text-gray-400 font-medium animate-pulse">Ricerca spazio locale...</div>
            </div>
        );
    }

    // Error states
    const errorConfig = {
        permission_denied: {
            title: "Permessi Richiesti",
            message: "Per identificare la tua posizione, l'app ha bisogno del permesso di localizzazione.",
            hint: "Vai nelle Impostazioni del telefono e abilita i permessi di localizzazione."
        },
        wifi_disconnected: {
            title: "WiFi Non Connesso",
            message: "Devi essere connesso alla rete WiFi del locale per accedere alla chat.",
            hint: "Connettiti al WiFi del treno o del locale e riprova."
        },
        tenant_not_found: {
            title: "Accesso Negato",
            message: "Impossibile identificare lo spazio chat automaticamente.",
            hint: "Assicurati di essere connesso al WiFi del servizio (es. WiFi Treno)."
        },
        error: {
            title: "Errore",
            message: errorMessage || "Si è verificato un errore imprevisto.",
            hint: "Riprova più tardi."
        },
        idle: { title: "", message: "", hint: "" }
    };

    const config = errorConfig[state as keyof typeof errorConfig];

    return (
        <div className="h-screen w-full flex items-center justify-center bg-gray-50/50 backdrop-blur-sm p-4 animate-in fade-in zoom-in duration-300">
            <div className="text-center p-8 bg-white rounded-[2.5rem] shadow-2xl border border-gray-100 max-w-md">
                <div className="w-20 h-20 mx-auto mb-6 flex items-center justify-center rounded-2xl bg-gray-50">
                    <img src="/local_logo.svg" alt="Local Logo" className="w-12 h-12 object-contain" />
                </div>
                <h1 className="text-2xl font-bold text-gray-900 mb-2">{config.title}</h1>
                <p className="text-gray-600 mb-2 leading-relaxed">{config.message}</p>
                <p className="text-gray-400 text-sm italic">{config.hint}</p>

                <div className="flex flex-col gap-3 mt-8">
                    <button
                        onClick={initConnect}
                        className="w-full px-6 py-4 bg-emerald-500 text-white rounded-2xl font-bold hover:bg-emerald-600 transition-all shadow-lg shadow-emerald-200 active:scale-95"
                    >
                        Riprova
                    </button>
                    <button
                        onClick={() => setState('idle')}
                        className="w-full px-6 py-3 text-gray-400 font-medium hover:text-gray-600 transition-colors"
                    >
                        Torna alla home
                    </button>
                </div>
            </div>
        </div>
    );
}

export default function Home() {
    return (
        <Suspense fallback={
            <div className="h-screen w-full flex items-center justify-center bg-gray-50">
                <div className="animate-pulse text-gray-400 font-medium">Inizializzazione in corso...</div>
            </div>
        }>
            <HomeContent />
        </Suspense>
    );
}
